body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
  }
  

.share {
  position: absolute;
  width: 35px;
  height: 35px;
  right: 0;
  margin-top: 10px;
  margin-right: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border-radius: 100%;
  cursor: pointer;
  box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.123);
}

button.share:focus {
  outline: none;
  border: none;
}

button.share:hover { 
  outline: none;
  box-shadow: inset 2px 2px 8px 3px rgba(0, 0, 0, 0.123);
}

.share img {
  width: 20px;
  height: 20px;
}

.trees-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.children-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
}

.box-wrapper {
  margin: 10px;
  cursor: pointer;
}

.box { 
  position: relative;
  display: block;
  width: 510px;
  height: 470px;
  background: none;
  box-shadow: 2px 2px 8px 3px rgba(0, 0, 0, 0.123);
}

div.box-wrapper:focus { 
  outline: none;
  box-shadow: inset 2px 2px 8px 3px rgba(0, 0, 0, 0.123);
}

div.box-wrapper:hover { 
  outline: none;
  box-shadow: inset 2px 2px 8px 3px rgba(0, 0, 0, 0.123);
}

.box.parent {
  box-shadow: none;
  cursor: auto;
}

.center-circle {
  position: absolute;
  top: 16.5%;
  left: 22%;
  width: 60%;
  height: 40%;
  background: #A6BECF;
  border-radius: 50%;
  z-index: 2;
}

.center-circle-copy {
  position: absolute; 
  width: 100%;
  height: 100%;
  background: #A6BECF;
  border-radius: 50%;
  z-index: 2;
}

.fruties-container {
  position: absolute;
  width: 68%;
  height: 77%;
  z-index: 3;
  left: 16%;
  top: 15%;
}

.tree-frutie {
  position: absolute;
  width: 10%;
  height: 15%;
  border-radius: 50%;
  background: orange;
  z-index: 3;
  left: 20%;
  top: 20%;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.123);
}

.fruties-container :nth-child(2){
  left: 80%;
}

.fruties-container :nth-child(3){
  left: 50%;
  top: 60%;
}

